export default {
  state: {
    shared: {
      isShow: false,
      timeToHide: 6000,
      message: ''
    }
  },
  mutations: {
    setShared ({ shared }, payload) {
      shared.isShow = true
      shared.message = payload
    },
    clearShared ({ shared }) {
      shared.isShow = false
      shared.message = ''
    }
  },
  actions: {
    setShared ({ commit, state }, payload) {
      commit('setShared', payload)
    },
    closeShared ({ commit }) {
      commit('clearShared')
    }
  },
  getters: {
    getSharedInfo: state => {
      return state.shared
    }
  }
}
