<template>
<v-app>
  <v-app-bar
    app
    dense
    flat
    class="pl-5"
    clipped-left>

    <v-toolbar-title>СКФ МТУСИ</v-toolbar-title>
    <v-spacer></v-spacer>

    <v-btn icon @click="switchVuetifyTheme">
      <v-icon>{{getVuetifyTypeTheme ? 'mdi-weather-sunny' : 'mdi-weather-night'}}</v-icon>
    </v-btn>
    <v-btn icon @click="setShared('Данная функция появится в ближайшем времени!')">
      <v-icon>mdi-cog-outline</v-icon>
    </v-btn>
    <v-menu
    left
    bottom
    >
    <template v-slot:activator="{ on }">
      <v-btn icon v-on="on">
        <v-icon>mdi-account</v-icon>
      </v-btn>
    </template>
    <v-list>
      <v-list-item
        v-for="n in 2"
        :key="n"
        @click="() => {}"
      >
        <v-list-item-title>Option {{ n }}</v-list-item-title>
      </v-list-item>
    </v-list>
    </v-menu>
  </v-app-bar>

  <v-navigation-drawer
  app
  clipped
  width="320"
  v-model="drawer"
  >
    <v-list>
      <v-list-item link>
        <v-list-item-content>
          <v-list-item-title class="title">Приемная комиссия</v-list-item-title>
          <v-list-item-subtitle>sandra_a88@gmail.com</v-list-item-subtitle>
        </v-list-item-content>
      </v-list-item>
    </v-list>
    <v-divider></v-divider>
    <v-list nav>
      <v-list-item link v-for="(e, i) in menuLinks" :key="i">
        <v-list-item-icon>
          <v-icon>mdi-{{e.icon}}</v-icon>
        </v-list-item-icon>
        <v-list-item-title>{{e.title}}</v-list-item-title>
      </v-list-item>
    </v-list>
  </v-navigation-drawer>

  <!-- Sizes your content based upon application components -->
  <v-content>

    <!-- Provides the application the proper gutter -->
    <v-container>
      <!-- If using vue-router -->
      <router-view></router-view>
    </v-container>
  </v-content>

  <v-bottom-navigation
    class="d-md-none"
    app
  >
    <v-btn value="menu" @click="drawer = !drawer">
      <span>Menu</span>
      <v-icon>mdi-menu</v-icon>
    </v-btn>
  </v-bottom-navigation>
  <v-snackbar
  v-model="getShared.isShow"
  color="indigo darken-2"
  vertical
  right
  :timeout="getShared.timeToHide"
  >
  {{getShared.message}}
  <v-btn
    text
    @click="closeShared"
  >
    Закрыть
  </v-btn>
  </v-snackbar>
</v-app>
</template>

<script>
export default {
  name: 'App',

  data: () => ({
    drawer: null,
    menuLinks: [
      { title: 'Очень длинная ссылка на какой-либо ресурс будет выглядеть примерно вот так', path: '', icon: 'folder' },
      { title: 'Обычная ссылка', path: '', icon: 'folder' }
    ]
  }),
  methods: {
    switchVuetifyTheme () {
      this.$vuetify.theme.dark = !this.getVuetifyTypeTheme
    },
    closeShared () {
      this.$store.dispatch('closeShared')
    },
    setShared (message) {
      this.$store.dispatch('setShared', message)
    }
  },
  computed: {
    getVuetifyTypeTheme () {
      return this.$vuetify.theme.dark
    },
    getShared () {
      return this.$store.getters.getSharedInfo
    }
  },
  beforeMount () {
    this.drawer = ['xs', 'sm'].indexOf(this.$vuetify.breakpoint.name) === -1
  }
}
</script>

<style lang="scss">
  .v-list-item__title {
    white-space: normal !important;
  }
</style>
